@import '../../common/styles/sass/variables';

html {
    scroll-behavior: smooth;
}

.nav {
    width: 300px;
    display: flex;
    justify-content: space-between;
}

.links {
    text-decoration: none;
    color: $bgColor;
    font-family: $textFontFamily;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 1px;
    &:hover {
        text-decoration: #20c997 underline dashed;
    }
}